import { createRouter, createWebHashHistory } from 'vue-router'
// import LoginView from '@/views/login/LoginView.vue'
// import ActInfoSrch from '@/views/login/ActInfoSrch.vue'

const routes = [
  { path: '/', component: () => import('@/views/login/LoginView.vue') },
  { path: '/ActInfoSrch', component: () => import('@/views/login/ActInfoSrch.vue') },
  { path: '/TermsInfo', component: () => import('@/views/login/TermsInfo.vue') },
  { path: '/TermsDetail', component: () => import('@/views/login/TermsDetail.vue') },
]

const router = createRouter({
  // history: createWebHistory(),
  history: createWebHashHistory(),
  routes,
})

export default router
