import { ref } from 'vue'

const value = ref('')

function onlyAlphaNumeric(e) {
  // ÏòÅÎ¨∏Ïûê(a-z, A-Z)ÏôÄ Ïà´Ïûê(0-9)Îßå ÌóàÏö©
  e.target.value = e.target.value.replace(/[^a-zA-Z0-9]/g, '')
  value.value = e.target.value
}

// 1. ÎπÑÏñ¥ÏûàÎäî Í∞í Ï≤¥ÌÅ¨
function isEmpty(value) {
  if (value == null) return true
  if (typeof value === 'string' && value.trim() === '') return true
  if (Array.isArray(value) && value.length === 0) return true
  if (typeof value === 'object' && Object.keys(value).length === 0) return true
  return false
}

// 1. ÎπÑÏñ¥ÏûàÏßÄ ÏïäÏùÄ Í∞í Ï≤¥ÌÅ¨
function isNotEmpty(value) {
  return !isEmpty(value)
}

// 2. Ïò§Îäò ÎÇ†Ïßú Î∞òÌôò
function getToday() {
  const today = new Date()
  return today.toISOString().split('T')[0]
}

// 3. Ïò§Îäò + ÌïúÎã¨ Îí§ ÎÇ†Ïßú Î∞òÌôò
function getTodayAndNextMonth() {
  const today = new Date()
  const nextMonth = new Date()
  nextMonth.setMonth(nextMonth.getMonth() + 1)
  return {
    today: today.toISOString().split('T')[0],
    nextMonth: nextMonth.toISOString().split('T')[0],
  }
}

// 4. ÌòÑÏû¨ ÏõîÏùò Ï≤´ÎÇ† + ÎßàÏßÄÎßâ ÎÇ†
function getFirstAndLastDayOfThisMonth() {
  const now = new Date()
  const firstDay = new Date(now.getFullYear(), now.getMonth(), 1)
  const lastDay = new Date(now.getFullYear(), now.getMonth() + 1, 0)
  return {
    firstDay: firstDay.toISOString().split('T')[0],
    lastDay: lastDay.toISOString().split('T')[0],
  }
}

// 5. Ïà´Ïûê Ï≤ú Îã®ÏúÑ ÏΩ§Îßà
function formatNumberWithComma(num) {
  if (isNaN(num)) return ''
  return num.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',')
}

// 6. Ï†ÑÌôîÎ≤àÌò∏ ÌòïÏãù Ï≤¥ÌÅ¨ (ÌïòÏù¥Ìîà Ìè¨Ìï® ÎòêÎäî 10~11ÏûêÎ¶¨ Ïà´Ïûê)
function validatePhoneNumber(phone) {
  // 1) Ìú¥ÎåÄÌè∞: 010ÏúºÎ°ú ÏãúÏûë, 10~11ÏûêÎ¶¨
  const mobileRegex = /^(010)(-?\d{3,4})(-?\d{4})$/

  // 2) ÏßÄÏó≠Î≤àÌò∏: 02 ÎòêÎäî 0ÏúºÎ°ú ÏãúÏûëÌïòÎäî 2~3ÏûêÎ¶¨ ÏßÄÏó≠Î≤àÌò∏
  //    Ïòà: 02-123-4567, 031-123-4567
  const areaRegex = /^(0(?:2|[3-6][1-5]|70|80|50))(-?\d{3,4})(-?\d{4})$/

  return mobileRegex.test(phone) || areaRegex.test(phone)
}

// 6.1 Ï†ÑÌôîÎ≤àÌò∏ ÌòïÏãù ÏßÄÏ†ï Ìï®Ïàò
function formatPhoneNumber(value) {
  console.log(value)
  const cleaned = value.replace(/\D+/g, '')

  // 1. ÏÑúÏö∏ (02) ÏßÄÏó≠Î≤àÌò∏
  if (cleaned.startsWith('02')) {
    if (cleaned.length <= 2) return cleaned
    if (cleaned.length <= 5) return `${cleaned.slice(0, 2)}-${cleaned.slice(2)}`
    if (cleaned.length <= 9)
      return `${cleaned.slice(0, 2)}-${cleaned.slice(2, 6)}-${cleaned.slice(6)}`
    return `${cleaned.slice(0, 2)}-${cleaned.slice(2, 6)}-${cleaned.slice(6, 10)}`
  }

  // 2. ÎÇòÎ®∏ÏßÄ ÏßÄÏó≠Î≤àÌò∏ Î∞è Ìú¥ÎåÄÌè∞ (3ÏûêÎ¶¨ ÏßÄÏó≠Î≤àÌò∏)
  if (cleaned.length <= 3) return cleaned
  if (cleaned.length <= 6) return `${cleaned.slice(0, 3)}-${cleaned.slice(3)}`
  if (cleaned.length <= 10)
    return `${cleaned.slice(0, 3)}-${cleaned.slice(3, 6)}-${cleaned.slice(6)}`
  return `${cleaned.slice(0, 3)}-${cleaned.slice(3, 7)}-${cleaned.slice(7, 11)}`
}

// 7. ÏÇ¨ÏóÖÏûêÎì±Î°ùÎ≤àÌò∏ ÌòïÏãù Ï≤¥ÌÅ¨ (10ÏûêÎ¶¨ Ïà´Ïûê)
function validateBizNumber(bizNum) {
  const regex = /^\d{10}$/
  return regex.test(bizNum)
}

// 8. Í∞ùÏ≤¥ ÍπäÏùÄ Î≥µÏÇ¨
function deepClone(obj) {
  return JSON.parse(JSON.stringify(obj))
}

// 9. Ïù¥Î©îÏùº ÌòïÏãù Ï≤¥ÌÅ¨
function validateEmail(email) {
  const regex = /^[\w-]+(\.[\w-]+)*@([\w-]+\.)+[a-zA-Z]{2,7}$/
  return regex.test(email)
}

// 10. ÎπÑÎ∞ÄÎ≤àÌò∏ Í∑úÏπô Ï≤¥ÌÅ¨
// Ïà´Ïûê, ÏòÅÎ¨∏Ïûê, ÌäπÏàòÎ¨∏Ïûê Ï§ë 2Í∞ÄÏßÄ Ïù¥ÏÉÅ Ìè¨Ìï® + 6~15Ïûê
function validatePasswordRule(password) {
  const lengthValid = /^.{6,15}$/.test(password)
  const types = [/[0-9]/.test(password), /[a-zA-Z]/.test(password), /[^a-zA-Z0-9]/.test(password)]
  const typeCount = types.filter(Boolean).length
  return lengthValid && typeCount >= 2
}

// 11. ÏòÅÎ¨∏Ïûê ‚Üí ÎåÄÎ¨∏Ïûê
function toUpperCase(str) {
  return typeof str === 'string' ? str.toUpperCase() : ''
}

// 12. ÏòÅÎ¨∏Ïûê ‚Üí ÏÜåÎ¨∏Ïûê
function toLowerCase(str) {
  return typeof str === 'string' ? str.toLowerCase() : ''
}

// 13. Ï£ºÏñ¥ÏßÑ ref Í∞ùÏ≤¥Îì§ÏùÑ ÏûêÎèôÏúºÎ°ú Ï¥àÍ∏∞ÌôîÌï®
// @param {Object} refs - key: ref Í∞ùÏ≤¥ (ex: { a: ref(), b: ref() })
function resetRefs(refs) {
  if (!refs || typeof refs !== 'object') return

  for (const key in refs) {
    const val = refs[key].value
    if (Array.isArray(val)) refs[key].value = []
    else if (typeof val === 'boolean') refs[key].value = false
    else if (typeof val === 'number') refs[key].value = 0
    else refs[key].value = ''
  }
}

function isValidbirthDtdate(dateStr) {
  if (!/^\d{8}$/.test(dateStr)) return false // Ïà´Ïûê 8ÏûêÎ¶¨Ïù∏ÏßÄ ÌôïÏù∏

  const year = parseInt(dateStr.substring(0, 4))
  const month = parseInt(dateStr.substring(4, 6)) - 1 // JSÏóêÏÑúÎäî 0~11Ïõî
  const day = parseInt(dateStr.substring(6, 8))

  const date = new Date(year, month, day)

  // ÎÇ†Ïßú Í∞ùÏ≤¥Î°ú Î≥ÄÌôòÌïú Í∞íÏù¥ ÏûÖÎ†•Í∞íÍ≥º ÏùºÏπòÌïòÎäîÏßÄ Í≤ÄÏÇ¨
  return date.getFullYear() === year && date.getMonth() === month && date.getDate() === day
}

function toCamelCase(str) {
  return str.toLowerCase().replace(/_([a-z])/g, (g) => g[1].toUpperCase())
}

function toCamelCaseKeys(obj) {
  if (Array.isArray(obj)) {
    return obj.map(toCamelCaseKeys)
  } else if (obj !== null && typeof obj === 'object') {
    return Object.keys(obj).reduce((acc, key) => {
      acc[toCamelCase(key)] = toCamelCaseKeys(obj[key])
      return acc
    }, {})
  }
  return obj
}

function isInteger(value) {
  // Í∞íÏù¥ ÎπÑÏñ¥ ÏûàÏúºÎ©¥ false
  if (value === null || value === undefined || value === '') return false

  // Ïà´ÏûêÎ°ú Î≥ÄÌôò
  const num = Number(value)

  // NaN Ï≤¥ÌÅ¨ + Ï†ïÏàò Ïó¨Î∂Ä Î∞òÌôò
  return Number.isInteger(num)
}
