<template>
  <Teleport to="body">
    <Transition name="fade">
      <div v-if="internalVisible" class="modal-overlay" tabindex="0" ref="overlayRef" @keydown.stop>
        <div class="modal-content">
          <div class="modal-body">
            <p style="white-space: pre-line">{{ message }}</p>
          </div>
          <div class="modal-footer">
            <button class="btn btn-primary" ref="okBtnFcs" @click="handleConfirm">ÌôïÏù∏</button>
            <!-- <button
              class="btn btn-primary"
              @click="handleConfirm"
              :disabled="clickLocked"
            >
              ÌôïÏù∏
            </button> -->
          </div>
        </div>
      </div>
    </Transition>
  </Teleport>
</template>

<script setup>
import { ref, watch, defineProps, defineEmits, nextTick, onMounted } from 'vue'
// onMounted,
const props = defineProps({
  visible: Boolean,
  message: {
    type: String,
    default: 'ÏïåÎ¶º ÎÇ¥Ïö©Ïù¥ ÏóÜÏäµÎãàÎã§.',
  },
})

const emit = defineEmits(['close', 'confirm'])

const internalVisible = ref(props.visible)
const overlayRef = ref(null)
const clickLocked = ref(false) // üîê Ï§ëÎ≥µ ÌÅ¥Î¶≠ Î∞©ÏßÄÏö©
const okBtnFcs = ref(null)

// Ï†ÑÏó≠ Ï§ëÎ≥µ Î∞©ÏßÄ ÏÉÅÌÉú ÏÑ§Ï†ï (optional)
window.__alertOpen__ = true

// propsÎ°ú Î∞õÏùÄ visible Î≥ÄÍ≤Ω Í∞êÏßÄ
watch(
  () => props.visible,
  (val) => {
    internalVisible.value = val
    if (val) {
      clickLocked.value = false // Ïû¨ÌôúÏÑ±Ìôî
      window.__alertOpen__ = true
      nextTick(() => okBtnFcs.value?.focus())
    } else {
      window.__alertOpen__ = false
    }
  }
)

const handleConfirm = () => {
  if (clickLocked.value) return // Ïù¥Ï§ë Ïã§Ìñâ Î∞©ÏßÄ
  clickLocked.value = true

  emit('confirm')
  emit('close')

  // ÏïΩÍ∞ÑÏùò ÏßÄÏó∞ ÌõÑ unlock Ï≤òÎ¶¨ (ÏïàÏ†ÑÎßù)
  setTimeout(() => {
    clickLocked.value = false
    window.__alertOpen__ = false
  }, 500)
}

onMounted(() => {
  okBtnFcs.value?.focus()
})
</script>

